import React from "react"
import { Link, graphql, useStaticQuery } from "gatsby"

export default function NavBar() {
  const data = useStaticQuery(graphql`
    query SiteInfo {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)
  const { title } = data.site.siteMetadata
  return (
    <nav>
      <h1>{title}</h1>
      <div className="links">
        <Link to="/gatsby">Home</Link>
        <Link to="/gatsby/about">About</Link>
        <Link to="/gatsby/projects">Portfolio Project</Link>
        <Link to="/gatsby/blog">Blog </Link>
      </div>
    </nav>
  )
}
